<template>
  <div class="hello">
    <input type="text" name="username" v-model="input.username" placeholder="Username" /><br/>
    <input type="password" name="password" v-model="input.password" placeholder="Password" /><br/>
    <button type="button" v-on:click="login()">Login</button>
    <p>{{ token }}</p>
  </div>
</template>

<script>
import login from '../login'

export default {
  name: "LoginForm",
  props: {
    title: String
  },
  data: function () {
    return {
      input: {
        username: "",
        password: ""
      }
    }
  },
  methods: {
    login: function () {
      if(this.input.username != "" && this.input.password != "") {
        login.login(this.input.username, this.input.password)
        this.token = login.token
      } else {
        console.log("A username and password must be present");
      }
    }
  }
};
</script>

