import request from 'superagent'
import qs from 'qs'

const SERVER_URL = 'https://login.local.crdant.io:8443/';

export default {
    token: {},
    login: (username, password) => request
        .post("/authorize")
        .send(qs.stringify({ username: username, password: password, scope: 'openid', grant_type: 'password'}))
        // .then( res => {
        //     request.post
        // })
    ,
    logout: (username, password) => request.post("Logout.do")
}
